.container {
    width: 100%;
    height: 60px;
    padding-top: 24px;
    gap: 8px;
    overflow-x: auto;
    overflow-y: hidden;
    white-space: nowrap;
    display: flex;
    align-items: center;
    justify-content: space-between;
    scrollbar-width: none;
    -ms-overflow-style: none;
  }
  
  .container::-webkit-scrollbar {
    display: none;
  }
  
  .promotions {
    display: inline-flex;
    align-items: center;
  }
  
  .selector {
    display: inline-flex;
    align-items: center;
    gap: 8px;
  }
  
  .currencyBtn {
    height: 40px;
    border-radius: 16px;
    background-color: #fce4ec;
  }
  
  .currencyBtn:hover {
    background-color: #fce4ec;
  }
  
  .currencyText {
    font-weight: bold;
    color: #d81b60;
    font-size: 15px;
    width: 35px;
    padding-left: 8px;
    padding: 8px;
  }
  
  .currencyIcon {
    color: #d81b60;
    font-size: 40px;
    padding: 8px;
  }
  
  .flagBtn {
    height: 40px;
    border-radius: 16px;
    padding-top: 12px;
    background-color: #fce4ec;
  }
  
  .flagBtn:hover {
    background-color: #fce4ec;
  }
  
  .flagAvatar {
    width: 48px;
    height: 58px;
    padding: 8px;
    margin-left: 8px;
  }
  
  .flagIcon {
    color: #d81b60;
    font-size: 50px;
    width: 25px;
  }
  